import { Outlet } from "react-router-dom";
import { ReactFlowProvider } from "react-flow-renderer";

import {
  FlowDrawerProvider,
  FlowDataProvider,
  FlowDialogProvider,
  FlowServicesProvider,
  FlowNodeServicesProvider,
  FlowHistoryProvider,
} from "Contexts/ChatFlowDashboard";

const ChatFlowLayout = () => {
  return (
    <FlowHistoryProvider>
      <FlowDialogProvider>
        <FlowDrawerProvider>
          <FlowDataProvider>
            <FlowNodeServicesProvider>
              <FlowServicesProvider>
                <ReactFlowProvider>
                  <Outlet />
                </ReactFlowProvider>
              </FlowServicesProvider>
            </FlowNodeServicesProvider>
          </FlowDataProvider>
        </FlowDrawerProvider>
      </FlowDialogProvider>
    </FlowHistoryProvider>
  );
};

export default ChatFlowLayout;
